<template>
  <div id="map-charts"></div>
</template>

<script>
import echarts from 'echarts'

export default {
  name: 'MapChart',
  data() {
    return {
      migrate: {},
      city: '',
      constant_option: {
        title: {
          text: '',
          left: 15,
          top: 5,
        },
        xAxis: {
          type: 'value',
          position: 'top',
        },
        yAxis: {
          type: 'category',
          axisLabel: {
            formatter: function (params) {
              return `${params.slice(5)}`
            },
          },
          data: [],
        },
        tooltip: {},
        series: [
          {
            data: [],
            type: 'line',
          },
        ],
      },
    }
  },
  watch: {
    city: 'handleCityChange',
  },
  mounted() {
    this.initMap()
  },
  methods: {
    sortByDate(a, b) {
      return Date.parse(a) - Date.parse(b)
    },
    initMap() {
      const that = this
      const mychart = echarts.init(document.getElementById('map-charts'))
      mychart.showLoading()
      this.$axios
        .get('map/china-cities.json')
        .then((chinaJson) => {
          echarts.registerMap('china', chinaJson.data)
          let timeline_data = []
          this.$axios
            .get('json/migrate.json')
            .then((res) => {
              let data = res.data
              that.migrate = data
              mychart.hideLoading()
              let all_days = Object.keys(data)
              let days = all_days.filter((day) => {
                return (
                  Date.parse(day) >= Date.parse('2020-1-15') &&
                  Date.parse(day) <= Date.parse('2020-3-5')
                )
              })
              let sorted_days = days.sort(this.sortByDate)
              sorted_days.forEach((day) => {
                let day_data = data[day]
                  .filter((row) => row.to === '大连市')
                  .sort((a, b) => b.value - a.value)
                  .splice(0, 30)
                let res = day_data.map((row) => {
                  return {
                    name: row.from.replace('市', ''),
                    value: row.value,
                  }
                })
                timeline_data.push(res)
              })

              let option = {
                baseOption: {
                  timeline: {
                    axisType: 'category',
                    // realtime: false,
                    // loop: false,
                    autoPlay: false,
                    // currentIndex: 2,
                    playInterval: 1000,
                    // controlStyle: {
                    //     position: 'left'
                    // },
                    bottom: 15,
                    data: sorted_days,
                    label: {
                      show: true,
                    },
                  },
                  tooltip: {
                    triggerOn: 'click',
                    formatter: function (params) {
                      // console.log(params)
                      const { componentType, name } = params

                      if (componentType === 'series') {
                        if (params && params.data) {
                          const { name: city_name, value } = params.data
                          that.city = city_name
                          return `${city_name}: ${value}`
                        }
                      } else {
                        return `${name}`
                      }
                    },
                  },
                  visualMap: [
                    {
                      type: 'piecewise',
                      pieces: [
                        {
                          min: 0,
                          max: 15,
                        },
                        {
                          min: 15,
                          max: 50,
                        },
                        {
                          min: 50,
                          max: 100,
                        },
                        {
                          min: 100,
                          max: 200,
                        },
                      ],
                      bottom: 15,
                      realtime: false,
                      inRange: {
                        color: ['lightskyblue', 'orangered'],
                      },
                    },
                  ],
                  series: [
                    {
                      type: 'map',
                      map: 'china',
                      // roam: true,
                      label: {
                        show: false,
                      },
                      data: [],
                    },
                  ],
                },
                options: [],
              }
              for (let n = 0; n < timeline_data.length; n++) {
                option.options.push({
                  series: {
                    type: 'map',
                    map: 'china',
                    // roam: true,
                    label: {
                      show: false,
                    },
                    data: timeline_data[n],
                  },
                })
              }
              mychart.setOption(option)
            })
            .catch((err) => console.log(err))
        })
        .catch((err) => console.log(err))
    },
    handleCityChange() {
      let all_days = Object.keys(this.migrate)
      let days = all_days.filter((day) => {
        return (
          Date.parse(day) >= Date.parse('2020-1-15') &&
          Date.parse(day) <= Date.parse('2020-3-5')
        )
      })
      let sorted_days = days.sort((a, b) => {
        return Date.parse(a) - Date.parse(b)
      })

      let values = []
      sorted_days.forEach((day) => {
        let res = this.migrate[day].filter((row) => {
          return row.to === '大连市' && row.from === this.city + '市'
        })
        values.push(res[0].value)
      })
      this.constant_option.title.text = `大连-->${this.city}`
      this.constant_option.yAxis.data = sorted_days.reverse()
      this.constant_option.series[0].data = values.reverse()
      this.$emit('changeCity', this.constant_option)
    },
  },
}
</script>

<style scoped>
#map-charts {
  width: 100%;
  height: 95vh;
}
</style>